import React, { Component } from 'react';
import '../Styles/NavicationBar.css';
import { requestFrame, S } from 'selector_dom';
import { updateRenderState } from '..';
import FavoritePage from './FavoritePage';
import Settings from './Settings';
import Summary from './Summary';
import Measures from './Measures';

export default class NavicationBar extends Component {
  componentDidMount() {
    if (window.location.pathname === '/favorites') {
      S('#navFavouriteButtonFill').css({ fill: 'var(--main-color)' });
      this.animateNavButtion('#navFavoritePageButton');
    } else if (window.location.pathname === '/settings') {
      this.animateNavButtion('#navSettingsPageButton');
    } else if (window.location.pathname === '/summary') {
      this.animateNavButtion('#navSummaryPageButton');
    } else if (window.location.pathname === '/measures') {
      this.animateNavButtion('#navMeasurePageButton');
    } else if (window.location.pathname === '/') {
      this.animateNavButtion('#navMainPageButton');
    }
  }
  
  animateNavButtion = id => {
    S(id + '  span').animate(
      { transform: 'translate(-50%, -50%) scale(0)', opacity: '1' },
      { transform: 'translate(-50%, -50%) scale(1.5)', opacity: '0' },
      { cleanUp: true, dispalyNone: true }
    );
    S(id + ' svg').css({ fill: 'var(--main-color)' });
    S(id + '  p').animate({}, { fontSize: 'calc(12px + 6 * ((var(--vw) - 320px) / 680))', opacity: '1' });
  };

  mainPageHandle = () => {
    window.location.pathname !== '/'
      ? window.history.back()
      : requestFrame({ from: window.scrollY, to: 0, duration: 500, easingFunction: 'easeOutQuart' }, x =>
          window.scrollTo({ top: x })
        );
  };

  favoritePageHandle = () => {
    if (window.location.pathname !== '/favorites') {
      window.location.pathname === '/'
        ? window.history.pushState('', '', '/favorites')
        : window.history.replaceState('', '', '/favorites');

      updateRenderState([
        <FavoritePage key={Math.random() * 100} hashName={window.location.hash.split('#')[1]} />,
        <NavicationBar key={Math.random() * 100} />,
      ]);
    }
  };

  summaryPageHandle = () => {
    if (window.location.pathname !== '/summary') {
      window.location.pathname === '/'
        ? window.history.pushState('', '', '/summary')
        : window.history.replaceState('', '', '/summary');

      updateRenderState([<Summary key={Math.random() * 100} />, <NavicationBar key={Math.random() * 100} />]);
    }
  };

  measuresPageHandle = () => {
    if (window.location.pathname !== '/measures') {
      window.location.pathname === '/'
        ? window.history.pushState('', '', '/measures')
        : window.history.replaceState('', '', '/measures');

      updateRenderState([<Measures key={Math.random() * 100} />, <NavicationBar key={Math.random() * 100} />]);
    }
  };

  settingsPageHandle = () => {
    if (window.location.pathname !== '/settings') {
      window.location.pathname === '/'
        ? window.history.pushState('', '', '/settings')
        : window.history.replaceState('', '', '/settings');

      updateRenderState([<Settings key={Math.random() * 100} />, <NavicationBar key={Math.random() * 100} />]);
    }
  };

  render() {
    return (
      <nav id='navicationBar'>
        <div onClick={this.mainPageHandle} className='navElements' id='navMainPageButton'>
          <span></span>
          <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
            <path d='M4 22h-4v-4h4v4zm0-12h-4v4h4v-4zm0-8h-4v4h4v-4zm3 0v4h17v-4h-17zm0 12h17v-4h-17v4zm0 8h17v-4h-17v4z' />
          </svg>
          <p>القائمة</p>
        </div>
        <div onClick={this.favoritePageHandle} className='navElements' id='navFavoritePageButton'>
          <span></span>
          <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
            <path d='M6.28 3c3.236.001 4.973 3.491 5.72 5.031.75-1.547 2.469-5.021 5.726-5.021 2.058 0 4.274 1.309 4.274 4.182 0 3.442-4.744 7.851-10 13-5.258-5.151-10-9.559-10-13 0-2.676 1.965-4.193 4.28-4.192zm.001-2c-3.183 0-6.281 2.187-6.281 6.192 0 4.661 5.57 9.427 12 15.808 6.43-6.381 12-11.147 12-15.808 0-4.011-3.097-6.182-6.274-6.182-2.204 0-4.446 1.042-5.726 3.238-1.285-2.206-3.522-3.248-5.719-3.248z' />
            <path
              id='navFavouriteButtonFill'
              fill='none'
              d='M12 4.248c-3.148-5.402-12-3.825-12 2.944 0 4.661 5.571 9.427 12 15.808 6.43-6.381 12-11.147 12-15.808 0-6.792-8.875-8.306-12-2.944z'
            />
          </svg>
          <p>المفضلة</p>
        </div>
        <div onClick={this.summaryPageHandle} className='navElements' id='navSummaryPageButton'>
          <span></span>
          <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
            <path d='M5 19h-4v-8h4v8zm6 0h-4v-18h4v18zm6 0h-4v-12h4v12zm6 0h-4v-4h4v4zm1 2h-24v2h24v-2z' />
          </svg>
          <p>الموجز</p>
        </div>
        <div onClick={this.measuresPageHandle} className='navElements' id='navMeasurePageButton'>
          <span></span>
          <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
            <path d='M19 0h-14c-2.762 0-5 2.239-5 5v14c0 2.761 2.238 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm3 19c0 1.302-.839 2.401-2 2.816v-2.816h-1v3h-2v-3h-1v3h-2v-3h-1v3h-2v-3h-1v3h-2v-3h-1v3h-2v-3h-1v2.815c-1.161-.414-2-1.513-2-2.815v-14c0-1.302.839-2.401 2-2.816v2.816h1v-3h2v6h1v-6h2v3h1v-3h2v3h1v-3h2v6h1v-6h2v3h1v-2.816c1.161.415 2 1.514 2 2.816v14zm-14.141-9h1.141v6h-1.354v-3.889h-1.488v-1c.893-.041 1.701-.201 1.701-1.111zm8.315 4.667h1.826v1.306h-3.922v-.958c1.529-1.491 2.379-2.244 2.389-3.018 0-.462-.268-.717-.754-.717-.453 0-.816.245-1.168.524l-.424-1.148c.545-.452 1.191-.656 1.833-.656 1.179 0 1.941.74 1.941 1.885.001 1.155-.899 2.043-1.721 2.782z' />
          </svg>
          <p>مقاييسك</p>
        </div>
        <div onClick={this.settingsPageHandle} className='navElements' id='navSettingsPageButton'>
          <span></span>
          <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'>
            <path d='M6 18h-2v5h-2v-5h-2v-3h6v3zm-2-17h-2v12h2v-12zm11 7h-6v3h2v12h2v-12h2v-3zm-2-7h-2v5h2v-5zm11 14h-6v3h2v5h2v-5h2v-3zm-2-14h-2v12h2v-12z' />
          </svg>
          <p>الاعدادات</p>
        </div>
      </nav>
    );
  }
}
